

import Vue from 'vue'
import FastClick from 'fastclick'
import App from './App'
import router from './router'
import vueresource from 'vue-resource'
Vue.use(vueresource)
import AlertPlugin from 'vux/src/plugins/alert/index.js'

Vue.use(AlertPlugin)
import VueAwesomeSwiper from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
Vue.use(VueAwesomeSwiper)
FastClick.attach(document.body)

Vue.config.productionTip = false
/* eslint-disable no-new */
new Vue({
router,
render: h => h(App)
}).$mount('#app-box')

Vue.http.post('http://act.guoanfamily.com/openweixin/jsapi/getJsapiSignature?local_url=' + encodeURIComponent(location.href.split('#')[0]),
{}).then(response => {
wx.config({
debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
appId: response.body.appid, // 必填，公众号的唯一标识
timestamp: parseInt(response.body.timestamp), // 必填，生成签名的时间戳
nonceStr: response.body.noncestr, // 必填，生成签名的随机串
signature: response.body.signature, // 必填，签名，见附录1
jsApiList: [
'onMenuShareAppMessage',
'onMenuShareTimeline'
] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
});
wx.ready(function () {


wx.onMenuShareAppMessage({
title: '国安城市第二届员工文化艺术节回顾', //标题
desc: '国安城市第二届员工文化艺术节回顾', //描述
link: 'http://mt.guoanfamily.com/poster/#/', //连接地址指打开分享时页面地址
imgUrl: 'https://img.guoanfamily.com/bj.png', //图片
trigger: function (res) {

},
success: function (res) {
},
cancel: function (res) {
},
fail: function (res) {
}
});

wx.onMenuShareTimeline({
title: '国安城市第二届员工文化艺术节回顾', //标题
desc: '国安城市第二届员工文化艺术节回顾', //描述
link: 'http://mt.guoanfamily.com/poster/#/', //连接地址指打开分享时页面地址
imgUrl: 'https://img.guoanfamily.com/bj.png', //图片
trigger: function (res) {

},
success: function (res) {
},
cancel: function (res) {
},
fail: function (res) {
}
});
wx.error(function (res) {
});
})
}, response => {
alert(err);
})



